.About {
  /* background: #fff; */
  /* background: rgba(255,255,255, .3); */
  background: rgba(100, 98, 131, .1);
  color: rgb(100, 98, 131);
  margin: 0 90px 90px 90px;
  padding: 20px;
  border: 1px dotted rgba(100,98,131,.1);
  border-radius: 5px;
  -webkit-box-shadow: 1px 2px 3px rgba(100,98,131,.3);
          box-shadow: 1px 2px 3px rgba(100,98,131,.3);
}
.body {

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:start;
      align-items:flex-start;
  max-width: 100%;
}
.img-circle {
  width: 120px;
  margin: 20px;
  /* margin-right:40px; */
  border: 1px solid rgba(0,0,0,.1);
  /* box-shadow: 1px 2px 3px rgba(0,0,0,.3); */
}
.about-description {
  margin-top: 25px;

}
.about-description p {
  font-size: 18px;
  text-align:justify;
  margin-right: 20px;
}
.TechStack {
  margin: 0 20px;
}
.technologies {
  /* background: rgba(100, 98, 131, .1); */
  background: rgb(255,255,255);
  border: 1px solid #e7e7e7;
  border-radius: 5px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align:center;
      align-items:center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 10px;
}
.clear {
  clear: both;
}
.logo-item {
    list-style: none;

    padding: 5px;
}
.logo-image {
  width: 100px;
}


@media screen and (max-width: 625px) {
  .body {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    /* padding-left: 20px;
    padding-right: -20px; */
    text-align: center;
    max-width: 100%;
  }
  .img-circle {
    margin-bottom: 0;
  }
  .footer {
    text-align: center;
  }
}



