body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  background: -webkit-linear-gradient(left, #fff 20px, transparent 1%) center,-webkit-linear-gradient(#fff 20px, transparent 1%) center,silver;
  background: -o-linear-gradient(left, #fff 20px, transparent 1%) center,-o-linear-gradient(#fff 20px, transparent 1%) center,silver;
  background: linear-gradient(90deg, #fff 20px, transparent 1%) center,linear-gradient(#fff 20px, transparent 1%) center,silver;
  background-size: 22px 22px;
}



